import React from "react";
import CodeBlock from "./codeBlock";
import AnchorTag from "./anchor";
import '../styles.css';

const replaceTitle = props => 
  (Array.isArray(props.children)) ? 
  props.children.map(c => typeof(c) === "string" ? c.replace(/\s+/g,'').toLowerCase() : c) 
  : 
  props.children.replace(/\s+/g,'').toLowerCase()

export default {
  h1: props => <h1 className='heading1' id={replaceTitle(props)} {...props} />,
  h2: props => <h2 className='heading2' id={replaceTitle(props)} {...props} />,
  h3: props => <h3 className='heading3' id={replaceTitle(props)} {...props} />,
  h4: props => <h4 className='heading4' id={replaceTitle(props)} {...props} />,
  h5: props => <h5 className='heading5' id={replaceTitle(props)} {...props} />,
  h6: props => <h6 className='heading6' id={replaceTitle(props)} {...props} />,
  p: props => <p className='paragraph' {...props} />,
  pre: props => <pre className='pre' {...props} />,
  code: CodeBlock,
  a: AnchorTag
  // TODO add `img`
  // TODO add `blockquote`
  // TODO add `ul`
  // TODO add `li`
  // TODO add `table`
};
